/* Bulma Utilities */
@media screen and (min-width: 1024px) {
  .container > .navbar .navbar-brand, .navbar > .container .navbar-brand.navbar-brand {
    margin-left: 0;
  }
}

.navbar-brand img, .navbar-brand svg {
  margin-right: 32px;
}

.navbar-burger {
  margin-right: -16px;
}

.masthead {
  padding-top: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid #DDD;
}

.global-footer .menu {
  margin-left: -16px;
}

.dialog-off-canvas-main-canvas {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

main {
  flex-grow: 1;
}

.container {
  width: 100%;
  padding-left: 32px;
  padding-right: 32px;
  margin-left: auto;
  margin-right: auto;
}
@media screen and (min-width: 769px), print {
  .container {
    max-width: 705px !important;
  }
}
@media screen and (min-width: 1024px) {
  .container {
    max-width: 960px !important;
  }
}
@media screen and (min-width: 1216px) {
  .container:not(.is-max-desktop) {
    max-width: 1152px !important;
  }
}

body.toolbar-tray-open.toolbar-vertical.toolbar-fixed {
  width: calc(100% - 15rem);
}

.tabs, .breadcrumb {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.notification_bar {
  padding-top: 16px;
  padding-bottom: 16px;
}